body { 
    background: #F2F2F2 ;
  }
  h1 {
    font-size:45px;
    text-align:center;
    margin:20px 0 0 0;
    position: absolute;
    z-index: 50;
    top: 70px;
    left: -15px;}
  h1 span { 
    left: 15px;
    position: relative;
  }
  .cloud-black {
    width:200px;
    height:220px;
    background:#000;
    border-radius:100px;
    -webkit-border-radius:100px;
    -moz-border-radius:100px;
    position:relative;
    margin:0 auto;
    z-index: 0;
    top: 20px;
  }
  .cloud-black:after, .cloud-black:before {
    content:'';
    position:absolute;
    background:#000;
    z-index:-1
  }
  .cloud-black:after {
   width:150px;
   height:150px;
   top:70px;
   left:-76px;
   border-radius:100px;
   -webkit-border-radius:100px;
   -moz-border-radius:100px;
  }
  .cloud-black:before {
   width:130px;
   height:130px;
   top:90px;
   right:-59px;
   border-radius:100px;
   -webkit-border-radius:100px;
   -moz-border-radius:100px;
  }
  .bottom-black {
   width:200px;
   position:absolute;
   bottom:0px;
   background:#000;
   z-index:-1;
   background: #000;
   height: 20px;
  }
  .cloud-inner {
    width:160px;
    height:180px;
    background: #F2F2F2;
    border-radius:100px;
    -webkit-border-radius:100px;
    -moz-border-radius:100px;
    position:relative;
    top: 20px;
    left: 20px;
    z-index: 10;
  }
  .cloud-inner:after, .cloud-inner:before { 
    content:'';
    position:absolute; 
    background: #F2F2F2; 
    z-index:-1
  }
  .cloud-inner:after {
    width:110px;
    height:110px;
    top:70px;
    left:-74px;
    background: #F2F2F2;
    border-radius:100px;
    -webkit-border-radius:100px;
    -moz-border-radius:100px;
  }
  .cloud-inner:before {
    width:90px;
    height:90px;
    top:90px;
    right:-59px;
    background: #F2F2F2;
    border-radius:100px;
    -webkit-border-radius:100px;
    -moz-border-radius:100px;
  }
  .bottom-inner {
    width:190px;
    position:absolute;
    bottom:0px;
    z-index:-1;
    background: #F2F2F2;
    height: 24px;
    left: -18px;
  }
  .break { 
    width: 80px;
    position: absolute;
    background: #F2F2F2;
    -moz-transform: skew(-40deg);
    -webkit-transform: skew(-40deg);
    height: 30px;
    bottom: -21px;
    left: 40px;
  }
  .thunder-bolt {
    width: 55px;
    height: 80px; 
    position: relative;
    -moz-transform: skew(-40deg);
    -webkit-transform: skew(-40deg);
    z-index: 20;
    left: 57px; 
    top: 5px;
    background: #f8b506;
  }
  .thunder-bolt:before, .thunder-bolt:after { 
    content:'';
    position: absolute;
    z-index: 30;
  } 
  